.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@import url("https://fonts.googleapis.com/css2?family=Balsamiq+Sans&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Balsamiq Sans", cursive;
}
body {
  background: url(/Images/bg5.jpg) no-repeat center fixed;
  background-size: cover;
}
/* Loading Animation */
#loader {
  background: #021f34 url(.\/Loading-Animation/rocket-3972.gif) no-repeat center
    center;
  background-size: 25%;
  height: 100vh;
  width: 100%;
  position: fixed;
  z-index: 100;
}

/* Navbar  */
ul li a {
  text-decoration: none;
  list-style: none;
  color: white;
  transition: all 0.2s ease-out;
}
nav ul {
  padding: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  list-style: none;
}
nav ul li {
  margin: 0 10px;
  padding: 0 5px;
  font-size: 115%;
  cursor: pointer;
  border-bottom: 3px solid transparent;
  transition: all 0.2s ease-out;
  text-transform: uppercase;
}
nav ul li a:hover {
  border-bottom: 3px solid white;
  letter-spacing: 1px;
  /* font-weight: bold; */
}
/* Main Content  */
section {
  height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
  /* text-align: center; */
  /* border: 2px solid black; */
}

.main {
  display: flex;
  justify-content: center;
  width: 77%;
  height: 80%;
  color: white;
  /* border: 2px solid white; */
  border-radius: 15px;
  backdrop-filter: blur(20px);
  /* background: rgba(0,0,0,0.8); */
  /* backdrop-filter: saturate(180%) blur(10px); */
  box-shadow: 0 0 60px rgb(33, 31, 31);
  background-color: #fafa33;
}
.content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
}
.content span {
  color: black;
  margin: 5px 0;
  margin-left: 80px;
  max-width: 50%;
  max-height: 100%;
  z-index: 100;
}
.content span:nth-child(1) {
  font-size: 35px;
}
.content span:nth-child(2) {
  font-size: 45px;
}
.content span:nth-child(3) {
  font-size: 30px;
}
.content span:nth-child(4) {
  font-size: 20px;
}
.about span:nth-child(1) {
  font-size: 20px;
  max-width: 100%;
}
.other {
  display: flex;
  align-items: center;
}
.other span {
  color: black;
  margin: 5px 0;
  margin-left: 80px;
  max-width: 50%;
  max-height: 100%;
  z-index: 100;
  font-size: 20px;
}
.mydescription {
  display: flex;
  flex-direction: column;
}
.socialmedia {
  display: flex;
  width: 100%;
  margin-left: 60px;
}
.socialmedia a img {
  width: 35px;
  margin: 10px 20px;
  transition: all 0.2s ease-in;
  cursor: pointer;
}
.socialmedia a img:hover {
  transform: scale(1.3);
}
/* Image  */
.image {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  /* border: 2px solid white; */
}
.image img {
  width: 70%;
}
form input {
  display: block;
  padding: 0.5rem;
  margin: 0.8rem auto;
  border-radius: 10px;
  border: none;
  width: 50%;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type="number"]::-moz-number-spin-up,
input[type="number"]::-moz-number-spin-down {
  display: none;
}
input[type="number"] {
  -moz-appearance: textfield;
}

.submit {
  display: block;
  margin: 1rem auto;
  padding: 0.7rem;
  border-radius: 10rem;
  border: none;
  background-color: #0c69b6;
  color: white;
  cursor: pointer;
}
.submit:hover {
  background-color: white;
  color: black;
  transition: 0.2s all ease-in;
  transform: scale(1.1);
}
