/* Media Queries */

@media screen and (max-width: 630px) {
  .main {
    flex-direction: column-reverse;
    width: 90%;
    height: 80%;
    align-items: center;
  }
  .socialmedia {
    margin-left: 0px;
    display: block;
  }
  .content {
    width: 100%;
    text-align: center;
  }
  .content span {
    margin: 5px auto;
    max-width: 59%;
  }
  .content span:nth-child(1) {
    font-size: 25px;
  }
  .content span:nth-child(2) {
    font-size: 32px;
  }
  .content span:nth-child(3) {
    font-size: 19px;
  }
  .content span:nth-child(4) {
    font-size: 17px;
  }
  .socialmedia a img {
    width: 30px;
  }
  nav ul li {
    font-size: 70%;
  }
  nav ul {
    padding: 0;
  }
  .image img {
    width: 100%;
  }
}

@media screen and (max-width: 280px) {
  .socialmedia {
    margin-left: -20px;
  }
}
